import * as actionTypes from '../actions/actionTypes';

const initialState = {
  token: null,
  userId: null,
  loading: false, 
  error: false,
  authDone: false
};

const reducer = (state = initialState, action) => {
  switch(action.type) {
    case actionTypes.AUTH_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        token: action.token,
        userId: action.userId,
        authDone: true
      };
    case actionTypes.AUTH_FAIL:
      return {
        ...state,
        loading: false,
        error: true
      };
    case actionTypes.AUTH_CLEAR_ERROR:
      return {
        ...state,
        error: false
      };
    case actionTypes.AUTH_LOGOUT:
      return {
        ...state,
        token: null,
        userId: null,
        authDone: false
      };
    default:
      return state;
  };
};

export default reducer;