import * as actionTypes from '../actions/actionTypes';

const initialState = {
  jobApplicationInfo: null,
  applicationFinished: false,
  error: false,
  applied: false
};

const reducer = (state = initialState, action) => {
  switch(action.type) {
    case actionTypes.JOB_APPLY_INIT:
      return {
        ...state,
        applied: false
      };
    case actionTypes.JOB_APPLY_SUCCESS:
      return {
        ...state,
        applicationFinished: true,
        error: false
      };
    case actionTypes.JOB_APPLY_FINISHED:
      return {
        ...state,
        applicationFinished: false,
        error: false,
        applied: true
      };
    case actionTypes.JOB_APPLY_FAIL:
      return {
        ...state,
        error: true,
        applied: false
      };
    case actionTypes.JOB_CLEAR_ERROR:
      return {
        ...state, 
        error: false
      };
    default:
      return state;
  }
};

export default reducer;