import * as actionTypes from './actionTypes';
import axios from 'axios';

export const jobApplyInit = () => {
  return {
    type: actionTypes.JOB_APPLY_INIT
  };
};

const jobApplySuccess = () => {
  return {
    type: actionTypes.JOB_APPLY_SUCCESS
  };
};

const jobApplyFinished = () => {
  return {
    type: actionTypes.JOB_APPLY_FINISHED
  };
};

const jobApplyFail = () => {
  return {
    type: actionTypes.JOB_APPLY_FAIL
  };
};

export const jobClearError = () => {
  return {
    type: actionTypes.JOB_CLEAR_ERROR
  };
};

export const jobApply = (info) => {
  return dispatch => {
    axios.post("https://pizza-land-babe6-default-rtdb.firebaseio.com/jobApply.json", info)
      .then(response => {

        dispatch(jobApplySuccess());
        setTimeout(() => {
          dispatch(jobApplyFinished());
        }, 2000);
      })
      .catch(error => {
        dispatch(jobApplyFail());
      })
  };
};